import React from 'react'
import { Field, FieldType, IFieldApi } from 'react-form'

import { RM } from '../helpers/ramda'
import { i18n } from '../i18n'
import { ISelectControlProps, SelectControl } from './SelectControl'

export interface IFormInputSelectProps<OptionType>
  extends ISelectControlProps<OptionType> {
  field: FieldType
  fieldIsPure?: boolean
  defaultValue?: OptionType
  isRequired?: boolean
  validatioName?: string
}

export class FormInputSelect<OptionType> extends React.Component<
  IFormInputSelectProps<OptionType>
> {
  static defaultProps = {
    validatioName: 'Value',
    getItemId: RM.prop('id'),
  }

  validate = (value: OptionType | undefined) => {
    if (this.props.isRequired && !value) {
      return i18n.t`${this.props.validatioName} must be selected`
    }
    return null
  }

  getControlProps(fieldApi: IFieldApi) {
    const { isMulti, options, getOptionValue, defaultValue } = this.props
    const selection = fieldApi.value || defaultValue

    if (isMulti) {
      const isSelected = Array.isArray(selection)
        ? (item: OptionType) => selection.includes(getOptionValue!(item))
        : RM.F
      return {
        value: Array.isArray(options)
          ? (options as OptionType[]).filter(isSelected)
          : [],
        onChange: selectedOptions => {
          fieldApi.setValue(selectedOptions.map(getOptionValue))
        },
      }
    }

    return {
      value:
        selection &&
        (options as OptionType[]).find(
          item => getOptionValue!(item) === selection,
        ),
      onChange: option => {
        fieldApi.setValue(getOptionValue!(option))
      },
    }
  }

  render() {
    const {
      field,
      fieldIsPure = false,
      isRequired = false,
      defaultValue,
      ...props
    } = this.props
    return (
      <Field field={field} validate={this.validate} pure={fieldIsPure}>
        {fieldApi => (
          <SelectControl
            {...this.getControlProps(fieldApi)}
            onFocus={() => fieldApi.setTouched(true)}
            styles={{
              control: base => {
                if (Boolean(fieldApi.error)) {
                  return {
                    ...base,
                    borderColor: 'red',
                  }
                }
                return base
              },
            }}
            {...props}
          />
        )}
      </Field>
    )
  }
}
