import React from 'react'
import { FieldType } from 'react-form'
import styled from 'styled-components'

import { FormInputSelect } from '../../../atoms/FormInputSelect'
import { buildQuery } from '../../../graph/buildQuery'
import { GZoneColors } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { Colors } from '../../../styling'
import { ZoneColorsQuery } from '../Zones.graphql'

interface IZoneColorSelectorProps {
  field?: FieldType
}

const ColorOption = styled.div`
  background-color: ${({ color }: { color: string }) => color};
  color: ${Colors.white};
  width: 6rem;
  padding: 0.4rem 1rem;
  margin: 0;
  height: 100%;
`

const QueryZoneColors = buildQuery<GZoneColors.Query, GZoneColors.Variables>(
  ZoneColorsQuery,
)

const makeStyles = () => ({
  option: base => ({
    ...base,
    padding: 0,
    margin: 0,
  }),
  menu: base => ({
    ...base,
    width: '8rem',
    maxWidth: '8rem',
  }),
})

export const ZoneColorSelector: React.FC<IZoneColorSelectorProps> = ({
  field = 'zoneColor.id',
}) => (
  <QueryZoneColors
    render={({ zoneColors }) => (
      <FormInputSelect<GZoneColors.ZoneColors>
        field={field}
        fieldIsPure={true}
        options={[...zoneColors]}
        width={8}
        styles={makeStyles()}
        defaultValue={RM.head(zoneColors)}
        getOptionValue={RM.prop('id')}
        formatOptionLabel={zoneColor => (
          <ColorOption color={zoneColor.color}>{zoneColor.code}</ColorOption>
        )}
      />
    )}
  />
)
