import React from 'react'

import { FCompanyBranchFragment } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { CompanyBranchDropdown } from '../../../molecules/CompanyBranchDropdown'

type TBranch = FCompanyBranchFragment

interface IProps {
  branches: TBranch[]
  selectedBranch: Maybe<TBranch>
  onSelection(branch: Maybe<TBranch>): void
  autoFocus: boolean
}

export const ZoneFilterBranch: React.FC<IProps> = ({
  branches,
  selectedBranch,
  onSelection,
  autoFocus,
}) => (
  <CompanyBranchDropdown
    placeholder={i18n.t`Select branch`}
    branches={branches}
    selectedBranch={selectedBranch}
    onSingleSelection={onSelection}
    formatOptionLabel={RM.prop('name')}
    isClearable={true}
    autoFocus={autoFocus}
  />
)
