import React from 'react'
import { SortableElement } from 'react-sortable-hoc'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import { DragHandle } from '../../../atoms/DragHandle'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FCompanyBranchFragment } from '../../../graph/generated'
import { GZone } from '../../../graph/types'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IZoneListCardProps {
  zone: GZone.Fragment
  branch: FCompanyBranchFragment

  onEdit(): void
}

const ZoneNameStyled = styled.div`
  font-size: 1.125rem;
  margin-top: 0.2rem;
`

const ZoneDescriptionStyled = styled.div`
  font-size: 0.875rem;
`
const ZoneFlexRowStyled = styled(FlexRowSingle)`
  height: ${Spacing.ButtonHeight};
`

export const ZoneListCard = SortableElement<IZoneListCardProps>(
  ({ zone, branch, onEdit }) => (
    <Card
      key={zone.id}
      padding={0}
      color={zone.enabled ? zone.zoneColor.color : Colors.greyMiddle}
      onClick={onEdit}
    >
      <ZoneFlexRowStyled>
        <FlexColumn fixedWidth={true} padding="0 0.5rem 0 0">
          <TouchButton
            width={Spacing.ButtonHeight}
            height={Spacing.ButtonHeight}
            iconName="mode_edit"
          />
        </FlexColumn>
        <FlexColumn widthGrowIndex={1}>
          <ZoneNameStyled>
            {zone.name}
            {` [P${zone.priority}]`}
          </ZoneNameStyled>
          <ZoneDescriptionStyled>{branch.name}</ZoneDescriptionStyled>
        </FlexColumn>
        <FlexColumn padding="0">
          <DragHandle />
        </FlexColumn>
      </ZoneFlexRowStyled>
    </Card>
  ),
)
