import { reaction } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import React, { useCallback, useRef, useState } from 'react'
import { Form, IFormApi } from 'react-form'
import Goodbye from 'react-goodbye'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { ConfirmDialog } from '../../../atoms/ConfirmDialog'
import { Loading } from '../../../atoms/Loading'
import { useRoot } from '../../../atoms/RootProvider'
import { logDebug } from '../../../helpers/logger'
import { RM } from '../../../helpers/ramda'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Spacing } from '../../../styling'
import { useZoneId } from '../hooks/useZoneId'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'
import { ZoneFormContent } from '../molecules/ZoneFormContent'
import { ZoneFormControls } from '../molecules/ZoneFormControls'
import { useZoneList } from './ZoneListLoader'

const DeleteButtonContainer = styled.div`
  width: ${Spacing.ButtonHeight};
  margin-top: ${Spacing.Middle};
`

export const ZoneFormPanel: React.FC = () => {
  const zoneId = useZoneId()
  const zoneList = useZoneList()
  const { zone: zoneModel } = useRoot()
  const { routeToZoneList } = useZoneRouteControl()
  const [isModified, setModified] = useState(false)
  const formApiRef = useRef<IFormApi | null>(null)
  const { enqueueError } = useSnackBar()

  const onSave = useCallback(async () => {
    if (formApiRef.current) {
      await formApiRef.current.submitForm()
      setModified(false)
      routeToZoneList()
    }
  }, [])

  const onDelete = useCallback(
    async () => {
      if (zoneId) {
        await zoneModel.destroyZone(zoneId)
      }
      routeToZoneList()
    },
    [zoneId],
  )

  useDisposable(() =>
    reaction(() => zoneModel.map.currentZonePoints !== null, setModified),
  )

  React.useEffect(
    () => {
      if (zoneList && zoneList.length === 0) {
        enqueueError(i18n.t`Requested zone wasn't found`)
        routeToZoneList()
      }
    },
    [zoneList],
  )

  const zone =
    zoneId && zoneList ? zoneList.find(RM.propEq('id', zoneId)) || null : null

  if (!zone) {
    return <Loading text={i18n.t`Loading form...`} />
  }

  return (
    <Form
      defaultValues={zone}
      onChange={formState => {
        logDebug('zone form change', formState.values)
        setModified(true)
      }}
      onSubmit={values => zoneModel.saveZone(zoneId!, values)}
      render={formApi => {
        formApiRef.current = formApi
        return (
          <Card padding={0}>
            <ZoneFormControls
              isSubmitting={formApi.submitting}
              isModified={isModified}
              onSave={onSave}
              onGoBack={routeToZoneList}
            />
            <ZoneFormContent />

            <CanMutate mutationName="destroyZone">
              <DeleteButtonContainer>
                <TouchButtonDelete
                  disabled={formApi.submitting}
                  confirmText={i18n.t`Remove zone ${zone.name}?`}
                  onDelete={onDelete}
                />
              </DeleteButtonContainer>
            </CanMutate>

            <Goodbye when={isModified}>
              {({ isShow, handleOk, handleCancel }) => (
                <ConfirmDialog
                  isShow={isShow}
                  confirmText={i18n.t`Do you want to cancel changes for the zone?`}
                  onConfirm={handleOk}
                  onCancel={handleCancel}
                />
              )}
            </Goodbye>
          </Card>
        )
      }}
    />
  )
}
