import * as R from 'ramda'
import React, { Fragment } from 'react'
import { SortableContainer, SortEndHandler } from 'react-sortable-hoc'
import styled from 'styled-components'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { FCompanyBranchFragment } from '../../../graph/generated'
import { GZone } from '../../../graph/types'
import { TZoneModel } from '../../../segments/zones/models/ZoneModel'
import { ZoneListCard } from '../atoms/ZoneListCard'

type TBranch = FCompanyBranchFragment

interface IZoneListProps {
  branches: TBranch[]
  zones: GZone.Fragment[]
  zoneModel: TZoneModel
  onZoneEdit(zoneID: ID): void
}

const ZoneListContainerStyled = SortableContainer(styled.div`
  margin-top: 0.5rem;
  cursor: arrow;
`)

const sortZones = R.sortWith<GZone.Fragment>([
  R.ascend(R.path(['companyBranch', 'name'])),
  R.descend(R.prop('priority')),
])

const groupByBranch = R.groupBy((zone: GZone.Fragment) => zone.companyBranch.id)

export const ZoneList: React.FC<IZoneListProps> = ({
  branches,
  zones,
  zoneModel,
  onZoneEdit,
}) => {
  const renderZone = (zone: GZone.Fragment, idx: number) => (
    <ZoneListCard
      key={zone.id}
      zone={zone}
      branch={branches.find(R.propEq('id', zone.companyBranch.id))!}
      index={idx}
      collection={zone.companyBranch.id}
      onEdit={() => onZoneEdit(zone.id)}
    />
  )
  const renderBranch = (branch: TBranch, branchZones: GZone.Fragment[]) => (
    <Fragment key={branch.id}>
      <CardListTitle>{branch.name}</CardListTitle>
      {branchZones.map(renderZone)}
    </Fragment>
  )

  let output
  let onSortEnd: SortEndHandler

  if (branches.length === 1) {
    const sorted = sortZones(zones)
    output = sorted.map(renderZone)
    onSortEnd = ({ oldIndex, newIndex }) => {
      const zone = sorted[oldIndex]
      zoneModel.changeZonePriority(zone, oldIndex, newIndex)
    }
  } else {
    const grouped = R.mapObjIndexed(sortZones, groupByBranch(zones))
    output = branches.map(branch => {
      const branchZones = grouped[branch.id]
      return branchZones ? renderBranch(branch, branchZones) : null
    })
    onSortEnd = ({ oldIndex, newIndex, collection }) => {
      const group = grouped[collection]
      const zone = group[oldIndex]
      zoneModel.changeZonePriority(zone, oldIndex, newIndex)
    }
  }

  return (
    <ZoneListContainerStyled
      useDragHandle={true}
      lockAxis="y"
      onSortEnd={onSortEnd}
    >
      {output}
    </ZoneListContainerStyled>
  )
}
