import React, { useCallback, useEffect, useRef } from 'react'
import { Polygon } from 'react-google-maps'

import { gpsToLatLng, latLngToGps } from '../../../helpers/gps'

interface IZoneMapAreaProps {
  points: ReadonlyArray<GeoPoint>
  color: string
  priority: number
  onChange(points: GeoPoint[]): void
  onClear(): void
}

export const ZoneMapEditableArea: React.FC<IZoneMapAreaProps> = props => {
  const polygonRef = useRef<Polygon | null>(null)

  const onAreaChange = useCallback(
    () => {
      if (polygonRef.current) {
        const path = polygonRef.current.getPath()
        props.onChange(path.getArray().map(latLngToGps))
      }
    },
    [props.onChange],
  )

  useEffect(() => {
    let listeners
    if (polygonRef.current && props.onChange) {
      const path = polygonRef.current.getPath()
      listeners = [
        path.addListener('set_at', onAreaChange),
        path.addListener('insert_at', onAreaChange),
        path.addListener('remove_at', onAreaChange),
      ]
    }
    return () => {
      props.onClear()
      if (listeners) {
        listeners.forEach(lis => lis.remove())
      }
    }
  }, [])

  const { points, color } = props
  return (
    <Polygon
      defaultPath={points.map(gpsToLatLng)}
      options={{
        fillColor: color,
        fillOpacity: 0.6,
        strokeWeight: 2,
        zIndex: 10000,
      }}
      editable={true}
      draggable={true}
      ref={polygonRef}
      onDragEnd={onAreaChange}
    />
  )
}
