import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GZones } from '../../../graph/types'
import { ZoneFragment } from '../Zones.graphql'

export const ZonesQuery = gql`
  query GZones($branchesId: [ID!]!) {
    zones(companyBranches: $branchesId) {
      ...GZone
    }
  }
  ${ZoneFragment}
`

export const QueryZones = buildQuery<GZones.Query, GZones.Variables>(ZonesQuery)
