import React from 'react'
import { Route, RouteComponentProps, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { ZonePageLayout } from '../../../segments/zones/pages/ZonePageLayout'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'

interface IZoneMenuRoutesProps extends RouteComponentProps<{}> {}

export const ZoneMenuRoutes: React.FC<IZoneMenuRoutesProps> = () => {
  const { getRouteToZone } = useZoneRouteControl()
  return (
    <Switch>
      <Route path={getRouteToZone(':zoneId?')} component={ZonePageLayout} />
      <Route component={NotFoundPage} />
    </Switch>
  )
}

export default ZoneMenuRoutes
