import { Observer } from 'mobx-react'
import { object } from 'prop-types'
import React, { useContext } from 'react'
import { GoogleMap, GoogleMapProps, withGoogleMap } from 'react-google-maps'
import { MAP } from 'react-google-maps/lib/constants'
import styled from 'styled-components'

import { TMapModel } from '../models/MapModel'
import { Radiuses } from '../styling'

interface IBaseMapProps extends GoogleMapProps {
  model: TMapModel
}

const MapContainerStyled = styled.div`
  border-radius: ${Radiuses.Card};
  overflow: hidden;
  height: 100%;
`

const BaseMapContainer = withGoogleMap(({ children }) => <>{children}</>)

const mapModelContext = React.createContext<TMapModel | null>(null)
const mapContext = React.createContext<google.maps.Map | null>(null)

class GoogleMapProvider extends React.PureComponent {
  static contextTypes = { [MAP]: object }
  private map: google.maps.Map
  constructor(props, context) {
    super(props)
    this.map = context[MAP]
  }
  render() {
    return (
      <mapContext.Provider value={this.map}>
        {this.props.children}
      </mapContext.Provider>
    )
  }
}

export const BaseMap: React.FC<IBaseMapProps> = ({ model, children }) => {
  const options: google.maps.MapOptions = {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
  }
  return (
    <mapModelContext.Provider value={model}>
      <BaseMapContainer
        containerElement={<MapContainerStyled />}
        mapElement={<div style={{ height: `100%` }} />}
      >
        <GoogleMapProvider>
          <Observer>
            {() => (
              <GoogleMap
                ref={model.setMapRef}
                zoom={model.zoom}
                center={model.center}
                onCenterChanged={model.updatePosition}
                onZoomChanged={model.updateZoom}
                options={options}
              >
                {children}
              </GoogleMap>
            )}
          </Observer>
        </GoogleMapProvider>
      </BaseMapContainer>
    </mapModelContext.Provider>
  )
}

export function useMapModel() {
  const model = useContext(mapModelContext)
  if (!model) {
    throw new Error('Trying to useMapModel, but BaseMap is missing in the tree')
  }
  return model
}

export function useGoogleMap() {
  const map = useContext(mapContext)
  if (!map) {
    throw new Error(
      'Trying to useGoogleMap, but BaseMap is missing in the tree',
    )
  }
  return map
}
