import { observer } from 'mobx-react-lite'
import React, { useContext } from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { GZone } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { QueryZones } from './ZoneListLoader.graphql'

interface IProps {}

export type TZone = GZone.Fragment

const ZoneListContext = React.createContext<Maybe<ReadonlyArray<TZone>>>(null)

export const ZoneListLoader: React.FC<IProps> = observer(({ children }) => {
  const { zone } = useRoot()
  const branches = zone.branches.filter(zone.branchFilterPredicate)
  if (branches.length === 0) {
    return (
      <ZoneListContext.Provider value={[]}>{children}</ZoneListContext.Provider>
    )
  }
  return (
    <QueryZones
      variables={{ branchesId: branches.map(RM.prop('id')) }}
      renderLoading={false}
      render={({ zones }, { loading }) => (
        <ZoneListContext.Provider value={loading ? null : zones}>
          {children}
        </ZoneListContext.Provider>
      )}
    />
  )
})

export const useZoneList = () => {
  return useContext(ZoneListContext)
}
