import { useRouterContext } from '@speedlo/hooks'

import { useRoot } from '../../../atoms/RootProvider'

export function useZoneRouteControl() {
  const { user, zone: zoneModel } = useRoot()
  const { history } = useRouterContext()
  const baseRoute = '/settings/zones'
  const getRouteToZone = (zoneId: ID) => {
    return `${baseRoute}/${zoneId}`
  }
  return {
    getRouteToZone,
    routeToZoneEdit(editZoneId: ID) {
      zoneModel.map.panToZone(editZoneId)
      if (user.canQuery('zone')) {
        history.push(getRouteToZone(editZoneId))
      }
    },
    routeToZoneList() {
      history.push(baseRoute)
    },
  }
}
