import React, { useEffect } from 'react'

interface IProps {
  isShow: boolean
  confirmText: string
  onConfirm(): void
  onCancel(): void
}

export const ConfirmDialog: React.FC<IProps> = props => {
  useEffect(
    () => {
      if (props.isShow) {
        if (window.confirm(props.confirmText)) {
          props.onConfirm()
        } else {
          props.onCancel()
        }
      }
    },
    [props.isShow],
  )
  return null
}
