import React, { MouseEvent } from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  isSubmitting: boolean
  isModified: boolean
  onSave(e: MouseEvent<HTMLButtonElement>): void
  onGoBack(): void
}

export const ZoneFormControls: React.FC<IProps> = ({
  isSubmitting,
  isModified,
  onGoBack,
  onSave,
}) => (
  <FlexRowSingle>
    <FlexColumn widthGrowIndex={0.4} padding="0">
      <TouchButton
        color={Colors.greyMiddle}
        onClick={onGoBack}
        text={i18n.t`Back`}
        disabled={isSubmitting}
      />
    </FlexColumn>
    <FlexColumn widthGrowIndex={0.4} padding="0">
      <CanMutate mutationName="updateZone">
        <TouchButton
          color={Colors.green}
          text={i18n.t`Save zone`}
          onClick={onSave}
          disabled={isSubmitting || !isModified}
        />
      </CanMutate>
    </FlexColumn>
  </FlexRowSingle>
)
