import React, { Fragment } from 'react'
import { FormApi, NestedFieldComponentProps } from 'react-form'
import styled from 'styled-components'

import { GZonePriceRange } from '../../../graph/types'
import { TouchButton } from '../../../molecules/TouchButton'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Spacing } from '../../../styling'
import { ZonePlaceholderInput } from '../atoms/ZonePlaceholderInput'
import { ZonePriceInput } from '../atoms/ZonePriceInput'

interface IProps
  extends NestedFieldComponentProps<GZonePriceRange.Fragment[]> {}

const AddButtonContainer = styled.div`
  position: relative;
  top: -6rem;
`

const RemoveButtonContainer = styled.div`
  position: relative;
  top: -7rem;
  left: 16rem;
`

export const ZoneFormPriceRange: React.FC<IProps> = ({ fieldApi }) => {
  return (
    <>
      {fieldApi.values.map((value, idx, values) => (
        <Fragment key={idx}>
          <ZonePriceInput
            label="za"
            isDeliveryPrice={true}
            field={[idx, 'price', 'value']}
          />

          {idx === values.length - 1 ? (
            <ZonePlaceholderInput display="&infin;" label="do" />
          ) : (
            <ZonePriceInput
              field={[idx, 'toPrice', 'value']}
              autoFocus={true}
            />
          )}

          {values.length > 1 && (
            <RemoveButtonContainer>
              <TouchButtonDelete
                onDelete={() => fieldApi.removeValue('', idx)}
              />
            </RemoveButtonContainer>
          )}
        </Fragment>
      ))}
      <FormApi
        render={formApi => (
          <AddButtonContainer>
            <TouchButton
              disabled={formApi.submitting}
              width={Spacing.ButtonHeight}
              iconName="add_circle"
              onClick={() =>
                fieldApi.addValue('', {
                  price: { value: 0 },
                  toPrice: { value: 0 },
                })
              }
            />
          </AddButtonContainer>
        )}
      />
    </>
  )
}
