import React from 'react'
import { FieldType } from 'react-form'
import styled from 'styled-components'

import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { i18n } from '../../../i18n'
import { Colors } from '../../../styling'

interface IZoneInputProps {
  field: FieldType
  label?: string
  autoFocus?: boolean
  isDeliveryPrice?: boolean
}

const LabelStyled = styled.div`
  min-width: 2rem;
  display: inline-block;
`

const FormInputTextBoxStyled = styled(FormInputTextBox)`
  width: 3rem;
  position: relative;
  z-index: 4;
`
const FormInputTextBoxDeliveryStyled = styled(FormInputTextBoxStyled)`
  color: ${Colors.blue};
`

const DeliveryContainer = styled.div`
  position: relative;
  left: 8rem;
  top: 0rem;
`

const PriceRangeContainer = styled.div`
  position: relative;
  left: 4rem;
  top: -0.3rem;
  width: 3rem;
`

const VerticalLine = styled.div`
  border-left: 3px solid #707070;
  width: 3px;
  position: relative;
  left: -2rem;
  height: 4rem;
  float: left;
  margin: -0.5rem 0 0 0;
`

const validatePrice = (value: string) => {
  if (typeof value === 'number') {
    return null
  }
  const trim = value.trim()
  return trim.length && Number.isNaN(Number.parseFloat(trim))
    ? i18n.t`Must be number`
    : null
}

export const ZonePriceInput = React.forwardRef<
  HTMLInputElement,
  IZoneInputProps
>(
  ({ field, label, isDeliveryPrice, autoFocus = false }, ref) =>
    isDeliveryPrice ? (
      <DeliveryContainer>
        <VerticalLine />
        <LabelStyled>{label}</LabelStyled>
        <FormInputTextBoxDeliveryStyled
          field={field}
          validate={validatePrice}
          // @ts-ignore
          ref={ref}
        />
      </DeliveryContainer>
    ) : (
      <PriceRangeContainer>
        <FormInputTextBoxStyled
          field={field}
          validate={validatePrice}
          autoFocus={autoFocus}
        />
      </PriceRangeContainer>
    ),
)
