import React from 'react'

import { SelectControl } from '../../../atoms/SelectControl'
import { FCompanyBranchBrand } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'

type TBrand = FCompanyBranchBrand

interface IProps {
  brands: ReadonlyArray<TBrand>
  selectedBrand: Maybe<TBrand>
  onSelection(brand: Maybe<TBrand>): void
}

export const ZoneFilterBrand: React.FC<IProps> = ({
  brands,
  selectedBrand,
  onSelection,
}) => (
  <SelectControl<TBrand>
    placeholder={i18n.t`Select brand`}
    options={brands as TBrand[]}
    value={selectedBrand}
    formatOptionLabel={RM.prop('name')}
    getOptionValue={RM.prop('id')}
    getOptionLabel={RM.prop('name')}
    isClearable={true}
    isSearchable={true}
    onChange={value => onSelection(value as Maybe<TBrand>)}
    autoFocus={true}
  />
)
