import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { ZoneFormPanel } from '../organisms/ZoneFormPanel'
import { ZoneListLoader } from '../organisms/ZoneListLoader'
import { ZoneMapControl } from '../organisms/ZoneMapControl'
import { ZoneMenuPanel } from '../organisms/ZoneMenuPanel'

interface IProps extends RouteComponentProps<{ zoneId?: ID }> {}

export const ZonePageLayout: React.FC<IProps> = ({ match }) => {
  const isEditing = Boolean(match.params.zoneId)
  return (
    <ZoneListLoader>
      <PageLayout
        pageTitle={i18n.t`Delivery zones`}
        useBackButton={true}
        backRoute="/settings"
      />
      <PageLayoutFullHeightItem
        widthInColumns={6}
        column={10}
        withScrollArea={isEditing}
      >
        {isEditing ? <ZoneFormPanel /> : <ZoneMenuPanel />}
      </PageLayoutFullHeightItem>
      <PageLayoutFullHeightItem widthInColumns={10}>
        <ZoneMapControl />
      </PageLayoutFullHeightItem>
    </ZoneListLoader>
  )
}
