import { observer } from 'mobx-react-lite'
import React from 'react'

import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { useZoneModel } from '../hooks/useZoneModel'
import { useZoneRouteControl } from '../hooks/useZoneRouteControl'

export const CreateZoneButton: React.FC = observer(() => {
  const zoneModel = useZoneModel()
  const { routeToZoneEdit } = useZoneRouteControl()
  return (
    <TouchButton
      text={
        zoneModel.hasBranchFilter
          ? i18n.t`Create zone for branch`
          : i18n.t`Select branch to create zone`
      }
      onClick={() => zoneModel.createZone(routeToZoneEdit)}
      disabled={!zoneModel.hasBranchFilter}
      color={Colors.turquoise}
      textNextToIcon={true}
      height={Spacing.ButtonHeight}
      iconName="add_circle"
    />
  )
})
