import { autorun } from 'mobx'
import { observer, useDisposable } from 'mobx-react-lite'
import React, { useCallback } from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { FCompanyBranchBrand } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { ZoneFilterBranch } from '../molecules/ZoneFIlterBranch'
import { ZoneFilterBrand } from '../molecules/ZoneFilterBrand'

export const ZoneFilter: React.FC = observer(() => {
  const { user, zone } = useRoot()
  const branches = user.branches

  const getBrandBranches = useCallback(
    (brand: Maybe<FCompanyBranchBrand>) =>
      brand === null
        ? []
        : user.branches.filter(RM.pathEq(['brand', 'id'], brand.id)),
    [],
  )

  useDisposable(() =>
    autorun(() => {
      if (branches.length === 1) {
        zone.setBranchFilter(RM.head(branches)!)
      }
    }),
  )

  // no filter is shown for less than 1 branch available
  if (branches.length <= 1) {
    return null
  }

  const singleBrand = user.brands.length === 1 ? RM.head(user.brands)! : null
  if (singleBrand) {
    zone.setBrandFilter(singleBrand, getBrandBranches(zone.brand))
  }

  return (
    <>
      {!singleBrand && (
        <ZoneFilterBrand
          brands={user.brands}
          selectedBrand={zone.brand}
          onSelection={brand => {
            if (brand === null) {
              zone.clearBrandFilter()
            } else {
              zone.setBrandFilter(brand, getBrandBranches(brand))
            }
          }}
        />
      )}
      {!singleBrand && <VerticalGap />}
      <ZoneFilterBranch
        branches={getBrandBranches(zone.brand)}
        selectedBranch={zone.branch}
        autoFocus={Boolean(singleBrand)}
        onSelection={branch => {
          if (!branch) {
            zone.clearBranchFilter()
          } else {
            zone.setBranchFilter(branch)
          }
        }}
      />
    </>
  )
})
