import React, { useCallback } from 'react'
import { Polygon } from 'react-google-maps'

import { gpsToLatLng } from '../../../helpers/gps'

interface IZoneMapAreaProps {
  points: ReadonlyArray<GeoPoint>
  color: string
  priority: number
  onFocus?(): void
}

export const ZoneMapArea: React.FC<IZoneMapAreaProps> = props => {
  const onAreaFocus = useCallback(
    (e: google.maps.PolyMouseEvent) => {
      if (props.onFocus) {
        e.stop()
        props.onFocus()
      }
    },
    [props.onFocus],
  )

  const { points, color, priority } = props
  return (
    <Polygon
      defaultPath={points.map(gpsToLatLng)}
      options={{
        fillColor: color,
        fillOpacity: 0.3,
        strokeWeight: 0,
        zIndex: 5000 - priority,
      }}
      editable={false}
      draggable={false}
      onDblClick={onAreaFocus}
    />
  )
}
